<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="6">
          <el-input placeholder="" v-model="input3">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="order_number" width="300"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.order_pay === 1" type="success"></el-tag>
            <el-tag type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{scope.row.create_time | formatDate}}</span>
          </template>
        </el-table-column>
        <el-table-column label="">
          <el-tooltip class="item" effect="dark" content="" :enterable="false" placement="top">
            <el-button type="primary" icon="el-icon-edit" @click="showModifyAddress()"></el-button>
          </el-tooltip>
          <el-tooltip
            class="item"
            effect="dark"
            content=""
            :enterable="false"
            placement="top"
          >
            <el-button type="success" icon="el-icon-location" @click="showExpressStepDialog"></el-button>
          </el-tooltip>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="modifyAddressDialogVisible"
      width="50%"
      @close="modifyAddressDialogClose"
    >
      <el-form
        :model="modifyAddressForm"
        :rules="modifyAddressFormRules"
        ref="modifyAddressFormRef"
        label-width="100px"
      >
        <el-form-item label="/" prop="provice">
          <!-- <el-input v-model="modifyAddressForm.provice"></el-input> -->
          <el-cascader
            :options="cityOptions"
            v-model="modifyAddressForm.provice"
            :value="city"
            @change="changeProvince"
            change-on-select
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="detail">
          <el-input v-model="modifyAddressForm.detail"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="modifyAddressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="modifyAddressDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="ExpressStepDialogVisible" width="50%">
      <!--  -->
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in expressInfo"
          :key="index"
          :timestamp="activity.time"
        >{{activity.context}}</el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityOptions from './city_data2017_element'
export default {
  data() {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      total: '',
      // 
      orderList: [],
      // 
      modifyAddressDialogVisible: false,
      // 
      modifyAddressForm: {
        provice: [],
        detail: ''
      },
      // 
      modifyAddressFormRules: {
        provice: [{ required: true, message: '', trigger: 'blur' }],
        detail: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      cityOptions: cityOptions,
      // 
      ExpressStepDialogVisible: false,
      // 
      expressInfo: []
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    // 
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.total = res.data.total
      this.orderList = res.data.goods
      console.log(this.orderList)
    },
    // 
    handleSizeChange(newPageSize) {
      this.queryInfo.pagesize = newPageSize
      this.getOrderList()
    },
    // 
    handleCurrentChange(newPageNum) {
      this.queryInfo.pagenum = newPageNum
      this.getOrderList()
    },
    // 
    showModifyAddress() {
      this.modifyAddressDialogVisible = true
    },
    // 
    changeProvince() {
      console.log(this.modifyAddressForm.provice)
    },
    // 
    modifyAddressDialogClose() {
      this.$refs.modifyAddressFormRef.resetFields()
    },
    // 
    showExpressStepDialog() {
      this.getExpressInfo()
      this.ExpressStepDialogVisible = true
    },
    // 
    async getExpressInfo() {
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.expressInfo = res.data
      console.log(this.expressInfo)
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../plugins/timeline/timeline.css';
@import '../../plugins/timeline-item/timeline-item.css';

.el-cascader {
  width: 100%;
}
</style>
